import React, { useState } from 'react';
// import { Row, Col } from 'react-bootstrap';
import '../../styles/Gallery.css';
import Masonry from 'react-masonry-css';
 
import img0 from '../../assets/images/img0.jpg';
import img1 from '../../assets/images/img1.jpg';
import img2 from '../../assets/images/img2.jpg';
import img3 from '../../assets/images/img3.jpg';
import img4 from '../../assets/images/img4.jpg';
import img5 from '../../assets/images/img5.jpg';

const Gallery = () => {

    const images = [img0, img1, img2, img3, img4, img5];

    const [isModalOpen, setIsModalOpen] = useState(false);
    const [currentImageIndex, setCurrentImageIdex] = useState(0);

    const openModal = (index) => {
        setCurrentImageIdex(index);
        setIsModalOpen(true);
    };

    const closeModal = () => {
        setIsModalOpen(false);
    }

    const nextImage = () => {
        setCurrentImageIdex((prevIndex) => (prevIndex + 1) % images.length);
    };

    const prevImage = () => {
        setCurrentImageIdex((prevIndex) => (prevIndex - 1 + images.length) % images.length);
    };

    // 이미지 레이아웃
    const breakpointColumnsObj = {
        default: 2, 
    }



    return (
        <div className='container'> 

        {/* Masonry 레이아웃 */}

        <Masonry
            breakpointCols={breakpointColumnsObj}
            className="my-masonry-grid"
            columnClassName="my-masonry-grid_column" 
        >
 
                {images.map((image, index) => (
                    <div className={`imgBox${index}`} key={index}>
                        <img 
                            src={image}
                            alt={`Image${index}`}
                            className='img-fluid' //rounded(보더 둥글게)
                            onClick={() => openModal(index)}
                            style={{ cursor: 'pointer' }}
                        />
                    </div>
                ))}
            </Masonry>

           {/* <div className='img_wrap_2'>
                {images.slice(3, 6).map((image, index) => (
                    <div className={`imgBox${index + 3}`} key={index + 3}>
                        <img
                            src={images[index + 3]}
                            alt={`Image_${index + 3}`}
                            className='img-fluid rounded'
                            onClick={() => openModal(index + 3)}
                            style={{ cursor: 'pointer' }}
                        />
                    </div>
                ))}
           </div> */}
 
            {isModalOpen && (
                <div className="modal-overlay" onClick={closeModal}>
                    <div className="modal-content" onClick={(e) => e.stopPropagation()}>
                        <span className="close-button" onClick={closeModal}>
                            &times;
                        </span>
                        <button className="prev-button" onClick={prevImage}>&#10094;</button>
                        <img src={images[currentImageIndex]} alt={`Modal Image`} className="modal-image" />
                        <button className="next-button" onClick={nextImage}>&#10095;</button>
                    </div>
                </div>
            )}
        </div>
    )

}


export default Gallery;