import React, { useEffect, useRef } from "react";
import "../styles/MyPage.css"; // 벚꽃 애니메이션을 위한 CSS 파일 추가

const CherryBlossom = () => {
  const containerRef = useRef(null);

  useEffect(() => {
    if (!containerRef.current) return;

    const defaults = {
      speed: 10,
      maxSize: 15,
      minSize: 10,
      newOn: 1000,
    };

    const wrap = containerRef.current;
    let wrapH = wrap.clientHeight;
    let wrapW = wrap.clientWidth;

    const getRandomRotate = () => {
      const rotateX = 360;
      const rotateY = Math.random() * 60 - 30;
      const rotateZ = Math.random() * 120 - 30;
      const translateX = Math.random() * 10 - 5;
      const translateY = Math.random() * 10 - 10;
      const translateZ = Math.random() * 15;
      return `rotateX(${rotateX}deg) rotateY(${rotateY}deg) rotateZ(${rotateZ}deg) 
              translateX(${translateX}px) translateY(${translateY}px) translateZ(${translateZ}px)`;
    };

    const randomSwayAnims = [...Array(10)].map(getRandomRotate);

    const applySwayAnim = (element) => {
      const randomSway = randomSwayAnims[Math.floor(Math.random() * randomSwayAnims.length)];
      element.style.transform = randomSway;
      setTimeout(() => applySwayAnim(element), 1000);
    };

    const petalGen = () => {
      if (!wrap) return;
      setTimeout(() => requestAnimationFrame(petalGen), defaults.newOn);

      const petal = document.createElement("span");
      petal.className = "petal";
      const size = Math.floor(Math.random() * (defaults.maxSize - defaults.minSize + 1)) + defaults.minSize;
      const startPosLeft = Math.random() * wrapW;
      const fallTime = (wrapH * 0.1 + Math.random() * 5) / defaults.speed;
      const horizontalOffset = Math.random() * 2 - 1;

      petal.style.width = `${size}px`;
      petal.style.height = `${size}px`;
      petal.style.left = `${startPosLeft}px`;
      petal.style.position = "absolute";
      petal.style.animation = `fall ${fallTime}s linear`;

      petal.addEventListener("animationend", () => {
        petal.remove();
      });

      wrap.appendChild(petal);

      const updatePos = () => {
        if (petal.parentNode) {
          petal.style.left = `${parseFloat(petal.style.left) + horizontalOffset}px`;
          requestAnimationFrame(updatePos);
        }
      };

      updatePos();
      applySwayAnim(petal);
    };

    window.addEventListener("resize", () => {
      wrapH = wrap.clientHeight;
      wrapW = wrap.clientWidth;
    });

    requestAnimationFrame(petalGen);

    return () => {
      window.removeEventListener("resize", () => {
        wrapH = wrap.clientHeight;
        wrapW = wrap.clientWidth;
      });
    };
  }, []);

  return <div ref={containerRef} className="cherry_blossom"></div>;
};

export default CherryBlossom;
