import React from 'react';
import '../../styles/Calendar.css';

const Calendar = () => {
 
    const currentYear = 2025;
    const currentMonth = 2; 

    const weddingDay = { date: 2, name: 'weddingDay'};
 

    const getDaysInMonth = (year, month) => {
        const date = new Date(year, month, 1);
        const daysInMonth = [];
        // const daysOfweek = [];

        // 해당월 첫 날의 요일
        const firstDay = date.getDay();

        // 해당 월 마지막 날짜
        const lastDate = new Date(year, month + 1, 0).getDate();

        // 첫 번째 주를 위한 빈 공간 추가
        let week = new Array(firstDay).fill(null);

        for (let i = 1; i <= lastDate; i++) {
            week.push(i);
            if (week.length === 7) {
                daysInMonth.push(week);
                week = [];
            }
        }

        if (week.length > 0) {
            daysInMonth.push(week);
        }

        return daysInMonth;
    };

    const calendarDays = getDaysInMonth(currentYear, currentMonth);

    const daysOfweek = ['Sun', 'Mon', 'The', 'Wed', 'Thu', 'Fri', 'Sat'];

    const isWeddingDay = (day) => {
        return weddingDay.date === day;
    }

    return (
        <div className='wrap'>
            <table className='Calendar'>
                <thead>
                    <tr>
                        {daysOfweek.map((day, index) => (
                            <th key={index} scope='col'>{day}</th>
                        ))}
                    </tr>
                </thead>
                <tbody>

                    {calendarDays.map((week, weekIndex) => (
                        <tr key={weekIndex}>
                            {week.map((day, dayIndex) => {
                                const isToday =
                                    day === new Date().getDate() &&
                                    currentYear === new Date().getFullYear() &&
                                    currentMonth === new Date().getMonth();

                                    const weddingday = isWeddingDay(day);
                                return (
                                    <td key={dayIndex}>
                                        <div className="inner">
                                            <span className={`day ${isToday ? 'today' : ''}${weddingday ? 'weddingday' : ''}`}>
                                                {day || ''}
                                                </span>
                                                {weddingday && <span className="special_day">{weddingday.name}</span>}
                                        </div>
                                    </td>
                                )
                            })}
                        </tr>
                    ))}


                </tbody>
            </table>
        </div>
    )
}

export default Calendar;