import React from 'react';
import Dropdown from './Dropdown';
import '../styles/MyPage.css';

const Contact = props => {

    const [firstDropdown, setFirstDropdown] = React.useState(false);
    const [secondDropdown, setSecondDropdown] = React.useState(false);


    // 복사붙여널기 
    const handleCopy = (text) => {
        navigator.clipboard.writeText(text)
        .then(()=> {
            alert('복사성공');
        })
        .catch(()=> {
            alert('복사실패');
        })
    }
    
 
    return (
        <div className='contact'>
             <p><span className='dot'></span> 00연락처 010-2222-1234</p>
            <button onClick={e => setFirstDropdown(!firstDropdown)}>

                {
                    firstDropdown
                    ? '계좌번호 ▲'
                    : '계좌번호 ▼'
                }

            </button>
            <Dropdown visibility={firstDropdown}>
                <ul className='dropdownUl'>
                    <li>oo은행 1002-222-132344 <span onClick={()=>{handleCopy('1002222132344')}}>계좌복사</span></li>
                    <li>oo은행 9938-11-025 <span onClick={()=>{handleCopy('993811025')}}>계좌복사</span></li> 
                </ul>
            </Dropdown>
            <br></br>
            <p><span className='dot'></span> 00연락처 010-2222-1234</p>
            <button onClick={e => setSecondDropdown(!secondDropdown)}>

                {
                    secondDropdown
                    ? '계좌번호 ▲'
                    : '계좌번호 ▼'
                }

            </button>
            <Dropdown visibility={secondDropdown}>
                <ul className='dropdownUl'>
                    <li>oo은행 786-99-14564 <span onClick={()=>{handleCopy('7869914564')}}>계좌복사</span></li>
                    <li>oo은행 3333-5555-4568 <span onClick={()=>{handleCopy('333355554568')}}>계좌복사</span></li> 
                </ul>
            </Dropdown>

        </div>
        
    )
}

 

export default Contact;
