import React, { useState, useEffect } from 'react';
import { db } from '../../firebase';  
import '../../styles/Guestbook.css';
import { collection, addDoc, query, orderBy, onSnapshot, serverTimestamp, deleteDoc, doc, getDoc } from 'firebase/firestore';
import CryptoJS from 'crypto-js';

function Guestbook() {
    const [name, setName] = useState('');
    const [password, setPassword] = useState('');
    const [message, setMessage] = useState('');
    const [entries, setEntries] = useState([]);
    const [deletePassword, setDeletePassword] = useState('');
    const [currentPage, setCurrentPage] = useState(1);
    const itemsPerPage = 6;
    const maxPageButtons = 6;

    useEffect(() => {
        const q = query(collection(db, "guestbook"), orderBy("timestamp", "desc"));
        const unsubscribe = onSnapshot(q, (snapshot) => {
            setEntries(snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() })));
        });

        return () => unsubscribe();
    }, []);

    const handleSave = async (e) => {
        e.preventDefault();

        if (name.trim() && message.trim() && password.length === 4) {
            const encryptedPassword = CryptoJS.SHA256(password).toString();
            await addDoc(collection(db, "guestbook"), {
                name,
                message,
                password: encryptedPassword,
                timestamp: serverTimestamp()
            });
            setName('');
            setPassword('');
            setMessage('');
        } else {
            alert("닉네임, 4자리 비밀번호, 메시지를 입력하세요!");
        }
    };

    const handleDelete = async (id) => {
        const docRef = doc(db, "guestbook", id);
        const docSnap = await getDoc(docRef);

        if (docSnap.exists()) {
            const storedPassword = docSnap.data().password;
            const enteredPassword = CryptoJS.SHA256(deletePassword).toString();

            if (enteredPassword === storedPassword) {
                await deleteDoc(docRef);
                setDeletePassword('');
                alert("메시지가 삭제되었습니다.");
            } else {
                alert("비밀번호가 일치하지 않습니다!");
            }
        }
    };

    const totalPages = Math.ceil(entries.length / itemsPerPage);
    const startIndex = (currentPage - 1) * itemsPerPage;
    const currentEntries = entries.slice(startIndex, startIndex + itemsPerPage);
    
    const startPage = Math.max(1, currentPage - Math.floor(maxPageButtons / 2));
    const endPage = Math.min(totalPages, startPage + maxPageButtons - 1);

    return (
        <div>
            <form onSubmit={handleSave}>
                <div className='g_book'>
                    <input 
                        type='text' 
                        placeholder='닉네임 입력' 
                        value={name}
                        maxLength={6}
                        onChange={(e) => setName(e.target.value)}
                    />
                    <input 
                        type='password' 
                        placeholder='비밀번호' 
                        maxLength={4}
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                    />
                    <div className='input_div'>
                        <textarea 
                            placeholder='메시지를 입력하세요.' 
                            maxLength={80} 
                            value={message}
                            onChange={(e) => setMessage(e.target.value)}
                        ></textarea>
                    </div>
                    <div className='guest_btn'>
                        <button type='submit'>저장</button>
                    </div>
                </div>
            </form>

            <div className='guestbook_entries'>
                <p>GuestBooks</p>
                {currentEntries.map((entry) => (
                    <div key={entry.id} className='guest_entry'>
                        <div className='entry_box'>
                            <div className='nickname'>{entry.name}</div> {entry.message}
                            <div className='can_box'>
                                <input 
                                    type='password' 
                                    placeholder='비밀번호' 
                                    maxLength={4}
                                    value={deletePassword}
                                    onChange={(e) => setDeletePassword(e.target.value)}
                                />
                                <button onClick={() => handleDelete(entry.id)}><span className='cancel_icon'>❌</span> 삭제</button>
                            </div>
                        </div>
                    </div>
                ))}
            </div>

            <div className='pagination'>
                {currentPage > 1 && (
                    <button onClick={() => setCurrentPage(currentPage - 1)}>&lt; </button>
                )}
                {Array.from({ length: endPage - startPage + 1 }, (_, index) => (
                    <button 
                        key={startPage + index} 
                        className={currentPage === startPage + index ? 'active' : ''} 
                        onClick={() => setCurrentPage(startPage + index)}
                    >
                        {startPage + index}
                    </button>
                ))}
                {currentPage < totalPages && (
                    <button onClick={() => setCurrentPage(currentPage + 1)}> &gt;</button>
                )}
            </div>
        </div>
    );
}

export default Guestbook;
