import React from 'react';
import '../../styles/Modal.css';


function Modal({ show, onClose, title, content, sms, tell }) {
    if (!show) return null;

    return (
        <div style={modalStyle}>
            <div style={backShadow} onClick={onClose}></div>
            <div style={modalContentStyle}>
                <h2>{title}</h2>
                <p>{content}</p>
                <a style={boxStyle} className='modalTxtBox' href={sms} >문자보내기</a>
                <a style={boxStyle} className='modalTxtBox' href={tell}>전화하기</a> 
                <button className='closeBtn' onClick={onClose}>닫기</button>
            </div> 
        </div>
    )
}


const modalStyle = {
    position: 'fixed',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    zIndex: '1',
};

const modalContentStyle = {
    backgroundColor: 'white',
    padding: '20px',
    borderRadius: '10px',
    textAlign: 'center',
    zIndex: 1,
};

const backShadow = {
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    backgroundColor: 'rgba(0,0,0,0.5)',
}

const boxStyle = {
    backgroundColor: 'rgb(242 242 242)',
    border: '1px solid #aaa',
    margin: '5px 0',
    padding: '10px',
    borderRadius: '10px',
}
 
export default Modal;